vm.PostPrintOrder2 = vm.PostPrintOrder2 || {};
vm.PostPrintOrder2.PickProductsPrint = function (dataParameter) {
    this.parentModel = null;
    var base = this;
    var cainiaoset = null;
    var CNPrint = null;
    var tablestart = "";
    var tr = "";
    var tableend = "";
    var printname = null;
    this.WaveRowId=null;

    this.excete = function (model, cnPrint) {
        CNPrint = cnPrint;
        tablestart = "";
        tr = "";
        tableend = "";
        cainiaoset = base.getDefaultPrintTemplate();
        if (cainiaoset == null || cainiaoset == "") {
            ko.c8showMessage('warn', "未设置打印模板");
            return;
        }
        //设置打印组件
        CNPrint.PRINT_INITA(cainiaoset.Top, cainiaoset.LLeft, 886, 480, "千胜后置打单拣货单打印");
        CNPrint.SET_PRINT_PAGESIZE(1, cainiaoset.Width, cainiaoset.Height, "");
        printname = cainiaoset.PrintName;
        CNPrint.SET_PRINTER_INDEX(printname);
        //var printModel=base.getFenJianPrintModel(model);
        var printModel=model;
        printModel.OrderInfo={};
        //printModel.OrderInfo.ExpressPrintDateMM = (new Date()).Format("yyyy-MM-dd hh:mm:ss")
        printModel.OrderInfo.WaveCode = printModel[0].WaveCode;
        printModel.OrderInfo.ShopRowID = printModel[0].ShopRowId;
        base.setcainiaoprint(printModel);
        if (printname != '0' && (printname == undefined || printname == null || printname == '')) {
            printname = CNPrint.SELECT_PRINTER();
        }
        CNPrint.SET_LICENSES("", "61A8E196CFE2FC08C91049FE374A647031", "", "");
        var isok = CNPrint.PRINT();
        if (isok) {
            ko.c8showMessage("打印完成");
        } else {
            ko.c8showMessage("打印错误");
        }
    }


    this.setcainiaoprint = function (data) {
        //data.OrderInfo['CodeBarCode']=data.OrderInfo['Code'];
        CNPrint.ADD_PRINT_DATA("ProgramData", cainiaoset.ParamStr);
        var ParamJson = JSON.parse(cainiaoset.ParamJson);
        for (var name in ParamJson) {
            if (ParamJson[name] == 1) {
                if (name == "TotalProductCount") {
                    continue;
                }
                if(name=="WaveCode"){
                    // var picsDisplay = "";
                    // picsDisplay += "<img src='"+data.OrderInfo[name]+"' width='30px' height='30px'/>";
                    // if (picsDisplay != "") {
                    //     CNPrint.SET_PRINT_STYLEA(name, "CONTENT", picsDisplay);
                    // }
                    CNPrint.SET_PRINT_STYLEA(name, "CONTENT", data.OrderInfo[name]);
                }else{
                    CNPrint.SET_PRINT_CONTENT(name, data.OrderInfo[name]);
                }

            }
        }
        //商品信息
        var productinfo = "";
        var totalproductcount = 0;
        if (tr == "") {
            var item = CNPrint.GET_VALUE("ItemContent", "productinfo");
            var startindex = item.indexOf("<thead>");
            startindex = startindex + 7;
            tablestart = item.substr(0, startindex);
            var endindex = item.indexOf("</thead>");
            tableend = item.substr(endindex + 8);
            tr = item.substr(startindex, endindex - startindex);
        }
        productinfo = productinfo + tablestart + tr + "</thead>";//表头
        var itemstr = JSON.parse(ParamJson.productinfo);
        var  productlist = data;
        $.each(productlist, function (i, item) {
            var trcolne = $.c8help.c8CloneObject(tr);
            for (var name in itemstr) {
                if (itemstr[name] == 1) {
                    if (name == "No") {
                        trcolne = trcolne.replace("序号", i + 1);
                    } else {
                        trcolne = trcolne.replace(base.transferToCn(name), item[name]==null?"":item[name]);
                    }
                }
            }
            productinfo += "<tbody>" + trcolne + "</tbody>";
        });
        productinfo += tableend;
        CNPrint.SET_PRINT_CONTENT("productinfo", productinfo);
    }

    this.transferToCn = function (cn) {
        var en = "";
        if (cn == "ProductCode") {
            en = "商品代码";
        } else if (cn == "ProductName") {
            en = "商品名称";
        }
        else if (cn == "ProductSkuCode") {
            en = "规格代码";
        } else if (cn == "ProductSkuName") {
            en = "规格名称";
        }
        else if (cn == "KwCode") {
            en = "库位名称";
        }
        else if (cn == "WarehouseName") {
            en = "仓库名称";
        }
        else if (cn == "RemainQuantity") {
            en = "数量";
        } else if (cn == "WaveCode") {
            en = "波次条码";
        } else if (cn == "ShopRowID") {
            en = "店铺";
        }
        return en;
    }

    this.getDefaultPrintTemplate = function () {
        var result = null;
        ko.c8Ajax({
            type: "GET",
            url: '/PrintTemplateCaiNiao/getPostPrintOrder2FenJian',
            async: false,
            datatype: "json",
            contentType: "application/json",
            success: function (data) {
                result = data;
            },
            error: function (e) {
                alert(e.responseText);
            },
            beforeSend: function () {
            },
            complete: function () {

            }
        });
        return result;
    }

};
